<template>
  <div>
    <van-sticky>
      <div class="header">
        <span @click="goBack">
          <i class="iconfont icon-fanhui goback"></i>
        </span>
        {{$route.name}}
      </div>
    </van-sticky>
    <!--  -->
    <div class="navBox">
      <div class="nav" ref="left">
        <ul>
          <li :class="{current:0==currentIndex}" @click="selectItem(0)"></li>
          <li :class="{current:1==currentIndex}" @click="selectItem(1)"></li>
          <li :class="{current:2==currentIndex}" @click="selectItem(2)"></li>
          <li :class="{current:3==currentIndex}" @click="selectItem(3)"></li>
        </ul>
      </div>
      <div class="nav2" ref="right">
        <ul>
          <li class="right-item-hook">
            <swiper :imgs="imglists"></swiper>
            <div class="brief">
              <h1>{{getRouterData.shop_name}}</h1>
              <p>
                <span class="red">{{getRouterData.shop_price}}.00</span>
                <span class="right"> 99 </span>
              </p>
            </div>
          </li>
          <li class="right-item-hook">
            <div class="detail">
              <h2></h2>
              <div v-for="(item, index) in imgs[0]" :key="index">
                <img v-lazy="item" alt />
              </div>
            </div>
          </li>
          <li class="right-item-hook">
            <div class="detail">
              <h2></h2>
              <div v-for="(item, index) in imgs[1]" :key="index">
                <img v-lazy="item" alt />
              </div>
            </div>
          </li>
          <li class="right-item-hook">
            <!--   -->
            <like></like>
          </li>
        </ul>
      </div>
    </div>
    <!-- end -->
    <!--  -->
    <van-goods-action>
      <van-goods-action-icon :info="num" icon="cart-o" text="" @click="$router.push('/shop')" />
      <van-goods-action-icon
        v-show="flag"
        icon="star-o"
        text=""
        color="#ff5000"
        @click="collectSuccess"
      />
      <van-goods-action-icon v-show="!flag" icon="star" text="" color="#ff5000" />
      <!-- <van-icon name="star-o" /> -->
      <van-goods-action-button color="#be99ff" type="warning" text="" @click="add(goods)" />

      <van-goods-action-button color="#7232dd" type="danger" text="" @click="onsubmit" />
    </van-goods-action>
    <loading v-if="loadingShow"></loading>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";
import { Toast } from "vant";
import like from "../public/like";
const { mapMutations, mapState } = createNamespacedHelpers("shop");
import BScroll from "@better-scroll/core";
import swiper from "../public/swiper";
import loading from "../../components/public/loading";
export default {
  data() {
    return {
      getRouterData: {},
      flag: true,
      goods: {},
      //
      imgs: [
        [
          require("../../assets/img/xq1.webp"),
          require("../../assets/img/xq2.webp"),
          require("../../assets/img/xq3.webp"),
          require("../../assets/img/xq4.webp"),
          require("../../assets/img/xq5.webp")
        ],
        [
          require("../../assets/img/xq6.webp"),
          require("../../assets/img/xq7.webp"),
          require("../../assets/img/xq8.webp"),
          require("../../assets/img/xq9.webp"),
          require("../../assets/img/xq10.webp"),
          require("../../assets/img/xq11.webp")
        ]
      ],
      //
      collect: {},
      //
      imglists: [
        {
          img:
            "https://resource.smartisan.com/resource/3bdfee2a8681c05708ea35b794ed33c4.png?x-oss-process=image/format,webp"
        },
        {
          img:
            "https://resource.smartisan.com/resource/1d5705f78be557549c23e7ad53ab1986.png?x-oss-process=image/format,webp"
        },
        {
          img:
            "https://resource.smartisan.com/resource/ae81bf77e959cd9b050d4831be3e374a.png?x-oss-process=image/format,webp"
        },
        {
          img:
            "https://resource.smartisan.com/resource/0c4d91fa627496a4e4cf60f97264120e.png?x-oss-process=image/format,webp"
        },
        {
          img:
            "https://resource.smartisan.com/resource/22d1d5b29e2821dc08d348282d13eb99.png?x-oss-process=image/format,webp"
        }
      ],
      scrollY: 0,
      listHeight: [], //li
      loadingShow: true
    };
  },
  methods: {
    goBack() {
      this.$router.push("/home");
    },
    //
    onsubmit() {
      if (this.$store.state.username) {
        this.add(this.getRouterData);
        this.$router.push("/order");
      } else {
        this.add(this.getRouterData);
        this.$router.push({
          path: "/login",
          query: {
            path: "/order"
          }
        });
      }
    },
    // 
    collectSuccess() {
      if (this.$store.state.username != "") {//
          this.flag = !this.flag;
          Toast("");
      }else{
        Toast("");
      }
    },
    //  
    ...mapMutations(["add"]),
    _initScroll() {
      // 
      this.lefts = new BScroll(this.$refs.left, {
        click: true, //
        probeType: 3 //
      });
      this.rights = new BScroll(this.$refs.right, {
        click: true,
        probeType: 3 //
      });

      // right
      this.rights.on("scroll", pos => {
        this.scrollY = Math.abs(Math.round(pos.y));
      });
    },

    _getHeight() {
      let rightItems = this.$refs.right.getElementsByClassName(
        "right-item-hook"
      );
      let height = 0; //
      this.listHeight.push(height); //
      // 
      for (let i = 0; i < rightItems.length; i++) {
        let item = rightItems[i];
        height += item.clientHeight; // 
        this.listHeight.push(height);
      }
    },
    // 
    // index 
    // event 
    selectItem(index) {
      // better-scrolleventevent_constructed
      // _constructedreturn
      // 
      let rightItems = this.$refs.right.getElementsByClassName(
        "right-item-hook"
      );
      let el = rightItems[index];
      // scrollToElement()//
      this.rights.scrollToElement(el);
      // 
      let leftItems = this.$refs.left.getElementsByTagName("li");
      let elLi = leftItems[index];
      this.lefts.scrollToElement(elLi);
    }
  },
  created() {
    // 
    this.collect = this.$route.query;
    // 500loading
    setTimeout(() => {
      this.loadingShow = false;
    }, 500);
    this.$store.state.isShow = false;
    if (this.$route.query) {
      this.getRouterData = this.$route.query;
      this.goods = this.$route.query;
      this.goods.yh = "99";
      this.goods.num = 1;
      this.imglists[0].img = this.getRouterData.shop_img;
    }
  },
  updated() {
    if (!this.flag) {
      this.$store.commit("collect/add", this.collect);
    }
  },
  destroyed() {
    // this.$store.state.isShow = true;
  },

  // 
  mounted() {
    //
    // html
    // 
    // $nextTick() DOM
    this.$nextTick(() => {
      this._initScroll(); //
      this._getHeight(); //
    });
  },
  components: {
    swiper, //
    like, //
    loading //
  },
  // 
  computed: {
    ...mapState({
      num: state => state.num
    }),
    currentIndex() {
      for (let i = 0; i < this.listHeight.length; i++) {
        let height = this.listHeight[i];
        let height2 = this.listHeight[i + 1];
        // height2heightheight2
        if (!height2 || (this.scrollY >= height && this.scrollY < height2)) {
          // 
          let leftItems = this.$refs.left.getElementsByTagName("li");
          let elLi = leftItems[i];
          this.lefts.scrollToElement(elLi);
          return i;
        }
      }
      // this.listHeight0
      return 0;
    }
  }
};
</script>

<style lang="scss">
.van-icon {
  vertical-align: middle;
}
</style>
<style lang="scss" scoped>
// 
.header {
  background-color: #1b1b1b;
  color: #ffffff;
  position: fixed;
  z-index: 9999;
  top: 0;
  left: 0;
  font-weight: bold;
  height: 1.066667rem;
  line-height: 1.066667rem;
  width: 100%;
  text-align: center;
  font-size: 0.4rem;
  position: relative;
  span {
    position: absolute;
    top: 50%;
    left: 20px;
    font-weight: normal;
    margin-top: -0.266667rem;
    font-size: 0.25rem;
    width: 1.09375rem;
    height: 0.533333rem;
    line-height: 0.533333rem;
    background: linear-gradient(#2a2a29, #151515);
    box-shadow: inset 0 0.013333rem 0.013333rem hsla(0, 0%, 100%, 0.1),
      inset 0 0 0.013333rem rgba(0, 0, 0, 0.5);
    border-radius: 0.08rem;
  }
  i.goback {
    width: 0.265625rem;
    height: 0.265625rem;
    font-size: 0.25rem;
  }
}
body {
  width: 100%;
}
//
.nav {
  z-index: 999;
  height: 1.2rem;
  top: 1.066667rem;
  ul {
    overflow: hidden;
    height: 98%;
    padding: 0 2%;
    // box-shadow: 0 5px 13px rgba(0,0,0,.12);
    li {
      margin-top: 5px;
      margin-bottom: 5px;
      height: 90%;
      background: #f2f2f2;
      width: 25%;
      float: left;
      font-size: 0.34375rem;
      font-weight: bold;
      border: 1px solid #e3e3e3;
      box-sizing: border-box;
      line-height: 1.1rem;
      text-align: center;
      color: #606060;
      &.current {
        color: #ffffff;
        background: #afafaf;
      }
    }
  }
}
.nav2 {
  position: absolute;
  top: 2.266667rem;
  bottom: 50px;
  overflow: hidden;
  width: 100%;
  ul {
    width: 100%;
    li {
      width: 100%;
      // height: 1000px;
      background-color: #e3e3e3;
    }
  }
}
//
.brief {
  overflow: hidden;
  margin-top: 0.15625rem;
  background-color: #fff;
  height: 2.8125rem;
  padding: 0 0.46875rem;
  h1 {
    height: 1.25rem;
    line-height: 1.25rem;
    font-size: 0.4375rem;
    color: #383838;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }
  p {
    height: 0.9375rem;
    line-height: 0.9375rem;
    .red {
      color: #d44d44;
      font-size: 0.375rem;
    }
    .right {
      margin-top: 0.078125rem;
      float: right;
      font-size: 0.3125rem;
      color: #7f7f7f;
    }
  }
}
//
.detail {
  overflow: hidden;
  background: #fff;
  h2 {
    border-top: 0.15625rem solid #e3e3e3;
    border-bottom: 0.015625rem solid #fafafa;
    color: #666666;
    text-indent: 0.46875rem;
    font-size: 26px;
  }
  img {
    display: block;
    width: 100%;
    min-height: 14.90625rem;
  }
}
</style>